import {Component, OnDestroy, OnInit} from '@angular/core';
import {ErrorDto} from "../model/error/error-dto";
import {Subject} from "rxjs";
import {NgxSpinnerService} from "ngx-spinner";
import {ToastrService} from "ngx-toastr";
import {StatisticsService} from "../service/statistics.service";
import {takeUntil} from "rxjs/operators";
import {GraphPieVo} from "../model/stats/graph-pie-vo";

@Component({
  selector: 'app-graph-home',
  templateUrl: './graph-home.component.html',
  styleUrls: ['./graph-home.component.css']
})
export class GraphHomeComponent implements OnInit, OnDestroy {

  errorDto: ErrorDto = new ErrorDto();
  destroy$ = new Subject();

  view: any[] = [700, 300];

  colorScheme = {
    domain: ['#5AA454', '#E44D25', '#CFC0BB', '#7aa3e5', '#a8385d', '#aae3f5']
  };
  /**
   * This class serves as demo angular charts
   * TODO: Orders
   *        * count of orders per last 24 hours, 1-30 days, 12- months
   *        * money sum per last 7 day
   *        * pie chart -> order statuses overal
   * TODO: Categories with product count
   *        *
   * TODO: Eshop overview
   */

  protected orderStatuses = []


  categoriesWithProducts = [
    { name: "Fantasy", value: 80 },
    { name: "Manga", value: 40 },
    { name: "Milostný román", value: 100 },
    { name: "Historické", value: 18 },
    { name: "Fotografie", value: 50 },
  ];

  single = [
    {
      "name": "Users",
      "value": 50
    },
    {
      "name": "Orders",
      "value": 37000
    },
    {
      "name": "Categories",
      "value": 72
    },
    {
      "name": "Products",
      "value": 5000
    },
    {
      "name": "Messages",
      "value": 15
    },
    {
      "name": "Total revenue",
      "value": 100000
    },
  ];

  multi = [
    {
      "name": "Germany",
      "series": [
        {
          "name": "30.1.2022",
          "value": 70000
        },
        {
          "name": "1.2.2022",
          "value": 10000
        },
        {
          "name": "2.2.2022",
          "value": 18000
        }
      ]
    },
  ];

  constructor(private statisticsService: StatisticsService,
              private spinner: NgxSpinnerService,
              private toastr: ToastrService) {
  }

  ngOnInit(): void {
    this.loadGraphs()
  }


  private loadGraphs() {
    this.spinner.show().then(r => r);
    this.statisticsService.getOrderPieStatus(this.errorDto)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: orderPieResponse => {
          this.spinner.hide().then(r => r);
          this.orderStatuses = orderPieResponse.map(responseItem => new GraphPieVo(responseItem.status, responseItem.total))
        },
        error: () => {
          this.spinner.hide().then(
            () => this.toastr.error(this.errorDto.httpStatus + ' load categories failed', 'Category'));
        }
      });
  }

  onSelect(event) {
    console.log(event);
  }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
}
